/* background */
.has-background {
    overflow: hidden;
    display: block;
    position: relative;
}

.background {
    height: 100%;
    width: 100%;
    position: absolute;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    z-index: 1;
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;

    & > img {
        display: none;
    }

    & + * {
        position: relative;
        z-index: 1
    }
}

/* pageloader */
.pageloader {
    background-color: rgba($primary-bg-color, 0.90);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    -moz-backdrop-filter: blur(10px);
    -ms-backdrop-filter: blur(10px);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    text-align: center;
}

