/* footer */
.fixed-footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    padding-bottom: 30px;
    padding-bottom: calc(env(safe-area-inset-bottom) + 30px);
    z-index: 1;
}

.tabs-footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    z-index: 9;
    background-color: rgba($footer-bg-color, 0.85);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    -moz-backdrop-filter: blur(10px);
    -ms-backdrop-filter: blur(10px);
    color: $footer-bg-text;
    transition-delay: 0.5s;
    -webkit-transition-delay: 0.5s;
    -moz-transition-delay: 0.5s;
    -ms-transition-delay: 0.5s;
    @extend .transition-05;

    &:before {
        content: "";
        width: 100%;
        display: block;
        border-radius: 0px;
        height: 15px;
        position: absolute;
        top: -15px;
        left: 0;
        right: 0;
        margin: 0 auto;
        opacity: 0.1;
        background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 100%);
        background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 100%);
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 100%);
    }

    .nav-tabs {
        border: 0;

        .nav-item {
            border: 0;

            .nav-link {
                border: 0;
                color: rgba($footer-bg-text, 0.3);
                padding: 0.94rem 1rem;
                position: relative;
                background-color: transparent;

                &:after {
                    content: "";
                    width: 90%;
                    display: block;
                    border-radius: 2px;
                    height: 3px;
                    background-color: $footer-primary-text;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    margin: 0 auto;
                    opacity: 0;
                }

                &.active {
                    color: $footer-primary-text;
                    background-color: transparent;

                    &:after {
                        opacity: 1;
                        @extend .transition-05;
                    }
                }
            }
        }
    }
}

/* sidebar */
.sidebar {
    width: 270px;
    position: fixed;
    top: 0;
    height: 100%;
    overflow-y: auto;
    display: block;
    z-index: 10;
    padding-bottom: calc(env(safe-area-inset-bottom) + 30px);
    padding-top: calc(env(safe-area-inset-top) + 30px);
    @extend .transition-05;


    &.sidebar-left {
        left: -285px;

        &.active {
            left: 0;
        }

    }

    .list-group {
        width: 100%;

        .list-group-item {
            border: 0;
            color: rgba($sidebar-bg-text, 0.4);
            background: none;

            i {
                font-style: normal;
                vertical-align: middle;
                margin-right: 15px;

                &:last-child,
                &.last {
                    margin-right: 0;
                    margin-left: 15px;
                    opacity: 0.5;
                    @extend .transition-05;
                }
            }

            .list-group {
                margin-top: 5px;

                .list-group-item {
                    padding-left: 0;
                    padding-right: 0;
                }
            }

            &.active {
                opacity: 1;
                background: none;
                color: $sidebar-bg-text;
            }

            &:focus {
                outline: none;
                box-shadow: none;
                background-color: rgba($sidebar-bg-text, 0.1);
            }

            &.multilevel {
                .multilevel-link {
                    display: block;
                }

                &.active {
                    .last {
                        transform: rotate(90deg);
                        -webkit-transform: rotate(90deg);
                        -moz-transform: rotate(90deg);
                        -ms-transform: rotate(90deg);
                    }
                }

                .multilevel-dropdown {
                    display: none;
                }
            }
        }

    }


    &.active {
        & + .main-container {
            transform: scale(1);
            -webkit-transform: scale(1);
            -moz-transform: scale(1);
            -ms-transform: scale(1);
        }
    }

    &.overlay-sidebar {
        box-shadow: 3px 0px 15px rgba(0, 0, 0, 0.1);
        -webkit-box-shadow: 3px 0px 15px rgba(0, 0, 0, 0.1);
        -moz-box-shadow: 3px 0px 15px rgba(0, 0, 0, 0.1);
        -ms-box-shadow: 3px 0px 15px rgba(0, 0, 0, 0.1);
        background: rgba($sidebar-bg-color, 0.85);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        -moz-backdrop-filter: blur(10px);
        -ms-backdrop-filter: blur(10px);

        &.active {
            & + .main-container {
                height: 100%;
                height: 100vh;
                transform: scale(1);
                -webkit-transform: scale(1);
                -moz-transform: scale(1);
                -ms-transform: scale(1);
            }
        }
    }

    &.reveal-sidebar {
        &.active {
            top: 0;

            & + .main-container {
                width: 100%;
                height: 100%;
                margin-left: 280px;
                transform: scale(0.9);
                -webkit-transform: scale(0.9);
                -moz-transform: scale(0.9);
                -ms-transform: scale(0.9);
                box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                -webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                -moz-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                -ms-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                overflow: hidden;
            }
        }
    }

    &.iconic-sidebar {
        width: 80px;
        left: -95px;

        .username {
            height: 40px;
            width: 40px;
        }

        .username-text,
        .text-link,
        .last {
            display: none;
        }

        .list-group {
            .list-group-item {
                text-align: center;

                i {
                    margin: 0 auto;
                }
            }
        }

        &.active {
            left: 0px;
            top: 0;

            & + .main-container {
                width: 100%;
                height: 100%;
                position: absolute;
                margin-left: 80px;
                transform: scale(1);
                -webkit-transform: scale(1);
                -moz-transform: scale(1);
                -ms-transform: scale(1);
                box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                -webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                -moz-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
                -ms-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
            }
        }
    }


    &.sidebar-right {
        right: -285px;

        &.active {
            right: 0;
        }

    }
}

.reveal-background:after {
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    background: rgba($sidebar-bg-color, 0.85);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    -moz-backdrop-filter: blur(15px);
    -ms-backdrop-filter: blur(15px);
}

.main-container {
    width: 100%;
    margin-left: 0;
    background-color: $bg-color;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1);
    -ms-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1);
    min-height: 100%;
    min-height: 100vh;
    z-index: 1;
    position: relative;
    @extend .transition-05;

    &.active {
        .tabs-footer {
            transition-delay: 0.0s;
            -webkit-transition-delay: 0.0s;
            -moz-transition-delay: 0.0s;
            -ms-transition-delay: 0.0s;
            bottom: -200px;
        }
    }
}

.sidebar-left-open,
.sidebar-right-open {
    overflow: hidden;
    width: 100%;
}

