    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    padding-bottom: env(safe-area-inset-bottom);
    line-height: 1rem;
}

.bottom-right {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 2;
    padding-bottom: env(safe-area-inset-bottom);
    line-height: 1rem;
}


/* button */
.btn {
    border: 0;

    i,
    span {
        vertical-align: middle
    }

    &:focus {
        outline: none;
        box-shadow: none;
    }
}

.btn-group-lg > .btn,
.btn-lg {
    padding: .565rem 1rem;
    font-size: 16px;
    line-height: 1.5;
}

.btn-group-sm > .btn,
.btn-sm {
    padding: 0.375rem .8rem;
    font-size: 12px;
    line-height: 20px;

    &.btn-rounded {
        width: 34px;
        height: 34px;
        text-align: center;
        padding: 6px 0;

        i {
            font-size: 21px;
            vertical-align: middle;
            margin-top: -2px;
        }
    }
}

.btn-20 {
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    padding: 0;

    i {
        font-size: 14px;
        vertical-align: middle;
        margin-top: -2px;
        height: 20px;
        line-height: 20px;
    }
}

.btn-40 {
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    padding: 0;

    i {
        vertical-align: middle;
        margin-top: -2px;
    }
}

.btn-default {
    background-color: $primary-btn-color;
    color: $primary-btn-text;

    &:focus,
    &:active,
    &:hover,
    &.active {
        background-color: darken($primary-btn-color, 10%);
        color: darken($primary-btn-text, 10%);
    }
}

.btn-light {
    background-color: $secondary-bg-color;
    color: darken($secondary-bg-color, 20%);

    &:focus,
    &:active,
    &:hover,
    &.active {
        background-color: darken($secondary-bg-color, 10%);
        color: darken($secondary-bg-color, 40%);
    }
}

.btn-white {
    background-color: $secondary-btn-color;
    color: $secondary-btn-text;

    &:focus,
    &:active,
    &:hover {
        background-color: darken($secondary-btn-color, 10%);
        color: darken($secondary-btn-text, 10%);
    }

    &.active {
        background-color: $primary-btn-color;
        ;
        color: $primary-btn-text;
    }
}

.continue {
    display: none
}

/* scroll up */
.scrollup {
    opacity: 0;
    display: none;
    z-index: 12;
    right: 15px;
    bottom: 65px;
    bottom: calc(65px + env(safe-area-inset-bottom));
    @extend .transition-05;

    &.active {
        opacity: 1;
        display: block;
    }

    &.atbottom {
        bottom: 75px;
    }
}

/* logo */
.logo-landing {
    max-width: 100px;
    height: auto;
}

.logo-header {
    display: inline-block;
    vertical-align: middle;

    .logo-img {
        vertical-align: middle;
        margin: 0;
        max-width: 44px;
        margin-right: 10px;
    }

    .logo-header-text {
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        font-size: 18px;
        line-height: 16px;

        small {
            font-size: 9px;
        }
    }
}

/* header */
.header {
    padding: 10px 0px;
    padding: calc(env(safe-area-inset-top) + 10px) env(safe-area-inset-right) 10px env(safe-area-inset-left);

    &.active {
        background: rgba($header-bg-color, 0.85);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        -moz-backdrop-filter: blur(10px);
        -ms-backdrop-filter: blur(10px);

    }

    .notification-point {
        top: 10px;
        right: 12px;
    }

    a,
    .btn-link {
        color: $header-bg-text
    }
}



/* float label */

.form-group {
    margin-bottom: 20px;

    .form-control-label {
        font-size: 13px;
        line-height: 18px;
    }
}

.float-label {
    padding-top: 15px;
    position: relative;

    .form-control-label {
        opacity: 0.5;
        position: absolute;
        left: 0;
        top: 22px;
        margin: 0;
        font-size: 15px;
        line-height: 20px;
        color: #000000;
        font-size: 15px;
        transition: ease all 0.5s;
        -webkit-transition: ease all 0.5s;
        -ms-transition: ease all 0.5s;
        -moz-transition: ease all 0.5s;
        z-index: 0;


    }

    .form-control,
    .chosen-container .chosen-choices,
    .chosen-container .chosen-single {
        background-color: transparent;
        border-width: 0 0 1px 0;
        border-radius: 0;
        z-index: 1;
        position: relative;
        padding-left: 0;
        padding-right: 0;
        background-image: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    }

    &.active {

        .form-control-label {
            top: 0;
            font-size: 11px;
            line-height: 16px;
        }
    }

    .form-control:focus {
        box-shadow: none;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        -ms-box-shadow: none;
        outline: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.6);

        & + .form-control-label {
            opacity: 0.5;
            top: 0;
            font-size: 11px;
            line-height: 16px;
        }
    }

    &.style-light {
        .form-control-label {
            color: #ffffff;
        }

        .form-control,
        .chosen-container .chosen-choices,
        .chosen-container .chosen-single {
            color: #ffffff;
            border-bottom: 1px solid rgba(255, 255, 255, 0.5);

            &::placeholder {
                color: rgba(255, 255, 255, 0.5)
            }

            &:focus {
                border-bottom: 1px solid rgba(255, 255, 255, 1);
            }
        }

        &.active {

            .form-control,
            .chosen-container .chosen-choices,
            .chosen-container .chosen-single {
                border-bottom: 1px solid rgba(255, 255, 255, 0.6);
            }
        }

    }
}

/* cart count */
.cart-count {
    background-color: $secondary-bg-color;
    color: $secondary-bg-text;
    border-radius: 5px;
    padding: 2px;
    width: 66px;

    .input-group-prepend,
    .input-group-append {
        .btn {
            height: 20px;
            width: 20px;
            line-height: 18px;
            padding: 0;
            border-radius: 4px;
            text-align: center;
            vertical-align: middle;
            background-color: lighten($secondary-bg-color, 5%);
            color: $secondary-bg-text;
        }
    }

    .form-control {
        padding: 0;
        line-height: 20px;
        height: 20px;
        text-align: center;
        max-width: 25px;
        vertical-align: middle;
        border-radius: 4px;
        border: 0;
        background: none;
        font-size: 12px;
    }

    &.cart-count-lg {
        width: 125px;

        .input-group-prepend,
        .input-group-append {
            .btn {
                height: 36px;
                width: 36px;
                line-height: 38px;
            }
        }

        .form-control {
            line-height: 36px;
            height: 36px;
            max-width: 50px;
            font-size: 18px;
        }
    }
}

/* chat list */
.chat-list {
    margin: 20px auto;

    .left-chat {
        margin-bottom: 15px;

        .chat-block {
            padding: 15px;
            border-radius: 4px 4px 4px 0px;
            margin-right: 3px;
            position: relative;
            width: auto;
            display: inline-block;
            background-color: lighten($primary-bg-color, 10%);
            color: $primary-bg-text;

            &:before {
                content: "";
                position: absolute;
                left: 0px;
                bottom: -16px;
                height: 8px;
                width: 8px;
                border-radius: 0;
                border-color: transparent;
                border-style: solid;
                border-width: 10px;
                z-index: 1;
                border-top-color: lighten($primary-bg-color, 10%);
                border-left-color: lighten($primary-bg-color, 10%);
            }
        }
    }

    .right-chat {
        margin-bottom: 15px;
        text-align: right;

        .chat-block {
            text-align: left;
            background: #ede9e9;
            padding: 15px;
            border-radius: 4px 4px 0px 4px;
            margin-left: 3px;
            position: relative;
            color: #999999;
            background-color: $secondary-bg-color;
            width: auto;
            display: inline-block;

            &:before {
                content: "";
                position: absolute;
                right: 0px;
                bottom: -16px;
                height: 8px;
                width: 8px;
                border-radius: 0;
                border-color: transparent;
                border-style: solid;
                border-width: 10px;
                border-right-color: $secondary-bg-color;
                border-top-color: $secondary-bg-color;
                z-index: 1;
            }

        }
    }
}

/* pre code style */
pre {
    background-color: rgba(132, 195, 255, 0.2);
    color: #84C3FF;
}

/* sidebar colors */
.btn.colorsettings {
    position: fixed;
    top: 50%;
    margin-top: -20px;
    bottom: auto;
    right: -15px;
    border-radius: 20px 0 0 20px !important;
    padding: 2px 15px 2px 5px;
    z-index: 10;
    @extend .transition-05;

    span {
        display: inline-block;
        vertical-align: middle;
        margin: 5px;
        animation: rotation 2s infinite linear;
        -moz-animation: rotation 2s infinite linear;
        -ms-animation: rotation 2s infinite linear;
        -webkit-animation: rotation 2s infinite linear;
    }

    &.active {
        right: 175px;
        border-radius: 20px 0 0 20px;
    }
}

.sidebar-right-color {
    width: 180px;
    position: fixed;
    right: -215px;
    top: 50%;
    margin-top: -175px;
    bottom: 0;
    height: 340px;
    overflow-y: auto;
    z-index: 9999;
    background-color: $sidebar-bg-color;
    color: $sidebar-bg-text;
    @extend .transition-05;
    box-shadow: 0 0px 10px rgba(0, 0, 0, 0.15);
    -webkit-box-shadow: 0 0px 10px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0 0px 10px rgba(0, 0, 0, 0.15);
    -ms-box-shadow: 0 0px 10px rgba(0, 0, 0, 0.15);
    border-radius: 15px 0 0 15px;

    &.active {
        right: 0;
    }
}


/* Color select */
.colorselect,
.colorselect2 {
    margin: 0 1px 1px 0;
    display: inline-block;
    text-align: center;
    cursor: pointer;
    border-radius: 4px;
    overflow: hidden;
    border: 4px solid rgba(0, 0, 0, 0);

    input[type="radio"] {
        display: none;

        & + label {
            display: block;
            height: 34px;
            line-height: 34px;
            width: 34px;
            color: #ffffff;
            margin-bottom: 0;
            cursor: pointer;
            border-radius: 4px;
            border: 4px solid rgba(255, 255, 255, 0);

            i {
                font-size: 16px;
                display: none;
                height: 24px;
                width: 24px;
                border-radius: 50%;
                text-align: center;
                line-height: 24px;
                background-color: green;
                vertical-align: middle;
            }
        }

        &:checked {
            & + label {
                border: 4px solid rgba(255, 255, 255, 0.3);

                i {
                    display: inline-block
                }
            }
        }
    }

    &.active {
        position: relative;
        border: 4px solid rgba(0, 0, 0, 0.1);
    }
}


.selectoption {
    margin: 0 5px 5px 0;
    display: inline-block;
    text-align: center;
    cursor: pointer;
    overflow: hidden;
    border: 4px solid rgba(0, 0, 0, 0.1);
    font-size: 8px;
    border-radius: 4px;
    text-align: center;
    background-color: #ffffff;

    input[type="checkbox"] {
        display: none;

        & + label {
            display: block;
            height: 34px;
            line-height: 24px;
            width: 34px;
            color: #666666;
            margin-bottom: 0;
            cursor: pointer;
            border-radius: 4px;
            border: 4px solid rgba(255, 255, 255, 0);
        }

        &:checked {
            & + label {
                color: #ffffff;
                @extend .btn-default;
                border: 4px solid rgba(255, 255, 255, 0.3);

                i {
                    display: inline-block
                }
            }
        }
    }

    &.active {
        position: relative;
        border: 4px solid rgba(0, 0, 0, 0.1);
    }

}


@keyframes rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(359deg);
    }
}

@-webkit-keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(359deg);
    }
}

@-moz-keyframes rotation {
    from {
        -moz-transform: rotate(0deg);
    }

    to {
        -wemozbkit-transform: rotate(359deg);
    }
}

@-ms-keyframes rotation {
    from {
        -ms-transform: rotate(0deg);
    }

    to {
        -ms-transform: rotate(359deg);
    }
}

/* RTL  */
.rtl {
    direction: rtl;
    text-align: right;

    .float-right {
        float: left !important;
    }

    .text-left {
        text-align: right !important;
    }

    .text-right {
        text-align: left !important;
    }

    .swiper-wrapper {

        .swiper-slide {
            direction: rtl
        }
    }

    .bottom-right {
        left: 0px;
        right: auto;
    }

    .bottom-left {
        right: 0px;
        left: auto;
    }

    .border-left {
        border-left: 0 !important;
        border-right: 1px solid #dee2e6 !important;
    }

    .col.pl-0 {
        padding-left: 15px !important;

    }

    .pl-0 {
        padding-right: 0 !important;
    }

    .ml-0,
    .mx-0 {
        margin-right: 0 !important;
    }

    .pl-2,
    .px-2 {
        padding-left: inherit !important;
        padding-right: .5rem !important;
    }

    .mr-auto {
        margin-right: 0 !important;
        margin-left: auto !important;
    }

    .ml-auto {
        margin-left: 0 !important;
        margin-right: auto !important;
    }

    .mx-auto {
        margin-right: auto !important;
        margin-left: auto !important;
    }

    .mr-2,
    .mx-2 {
        margin-left: .5rem !important;
        margin-right: 0 !important;
    }

    .mr-3,
    .mx-3 {
        margin-right: 0 !important;
        margin-left: 1rem !important;
    }

    .ml-3,
    .mx-3 {
        margin-left: 0 !important;
        margin-right: 1rem !important;
    }

    .mr-0,
    .mx-0 {
        margin-left: 0 !important;
    }



    .nav {
        padding-right: 0
    }

    .offerslidetab1 .swiper-pagination {
        padding-right: 25px;
        left: auto;
        right: 0;
    }

    .sidebar {
        &.sidebar-left {
            left: auto;
            right: -285px;

            &.active {
                right: 0;
                left: auto;
            }
        }
    }

    .sidebar-right-color {
        left: -215px;
        right: auto;
        border-radius: 0 15px 15px 0;

        &.active {
            left: 0;
            right: auto
        }
    }

    .btn.colorsettings {
        left: -15px;
        right: auto;
        border-radius: 0 20px 20px 0 !important;
        padding: 2px 5px 2px 15px;

        &.active {
            left: 175px;
            border-radius: 0 20px 20px 0;
        }
    }

    .sidebar.reveal-sidebar.active + .main-container {
        margin-left: 0;
        margin-right: 280px;
    }

    .sidebar.iconic-sidebar.active + .main-container {
        margin-left: auto;
        margin-right: 80px;
    }

    .float-label .form-control-label {
        left: auto;
        right: 0;
    }

    .sidebar.sidebar-right {
        right: auto;
        left: -285px;

        &.active {
            left: 0;
            right: auto;
        }
    }
}


/* ui-rounded */
.ui-rounded {
    .btn {
        border-radius: 30px;
    }

    .modal-content {
        border-radius: 20px;
    }


    .card {
        border-radius: 10px;

        .card-header:first-child {
            border-radius: 10px 10px 0 0;
        }

        .card-footer:last-child {
            border-radius: 0 0 10px 10px;
        }
    }

    .rounded {
        border-radius: 10px !important;
    }

    .avatar {
        border-radius: 50%;
    }

    .icon {
        border-radius: 8px;
    }

    .cart-count {
        border-radius: 12px;

        .input-group-prepend,
        .input-group-append {
            .btn {
                border-radius: 10px;
            }
        }

        &.cart-count-lg {
            border-radius: 22px;

            .input-group-prepend,
            .input-group-append {
                .btn {
                    border-radius: 22px;
                }
            }
        }

    }

}


::-webkit-scrollbar {
    width: 2px;
    height: 2px;
}

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background: rgba($primary-bg-color, 0.5);
}

::-webkit-scrollbar-track {
    border-radius: 5px;
    background: $secondary-bg-color;
}
